import Accounting from 'accounting'
import Dayjs from 'dayjs'
import Cookies from 'js-cookie'

export default {
  /**
   * localstrage
   */
  fetch(key) {
    return JSON.parse(window.localStorage.getItem(key) || '[]')
  },
  save(key, value) {
    window.localStorage.setItem(key, JSON.stringify(value))
  },

  /**
   * @param start  
   * @param end 
   * @param target 
   */
  replaceStr(target, start, end, length) {
    let str = ''
    if (start) {
      str = target.substr(start, length) + '***'
    } else if (end) {
      str = '***' + target.substr(end, length)
    } else {
      str = target.substr(0, 1) + '***' + target.substr(target.length - 1, 1)
    }
    return str
  },

  /**
   * Cookie
   */
  getCookie(key) {
    return Cookies.get(key)
  },
  setCookie(key, val) {
    if (typeof val === 'object') {
      val = JSON.stringify(val)
    }
    return Cookies.set(key, val)
  },
  removeCookie(key) {
    return Cookies.remove(key)
  },

  /**
   * fmt 
   * date 
   */
  formatDate(format, date) {
    return Dayjs(date).format(format)
  },

  /**
   * status.id => status.name
   * @param {*} id 
   * @param {*} status idname
   */
  formatStatus(id, status) {
    return status.map(function(item) {
      return item.id === id ? item.name : void 0
    })
  },

  /**
   * 
   * mss
   * flagfalsems -> s( / 1000), flagtrues -> ms( * 1000)
   */
  formatStamp(goalTimeStamp, flag) {
    flag = !!flag
    return flag ? goalTimeStamp * 1000 : goalTimeStamp / 1000
  },

  /**
   * 
   * keepDecimal 0
   */
  formatMoney(price, keepDecimal = false) {
    price = Accounting.formatNumber(price, 2)
    if (!keepDecimal) {
      price = price.replace(/(\.00|0)$/, '')
    }
    return price
  },

  /**
   * 
   * noDecimal 0
   */
  formatFloat(float, noDecimal) {
    float = parseFloat(float).toFixed(2)
    if (noDecimal) {
      float = float.replace(/(\.00|0)$/, '')
    }
    return float
  },

  /**
   * 0
   */
  padLeftZero(str) {
    return ('00' + str).substr(str.length)
  },

  /**
   * 
   */
  formatPhone(phone) {
    return phone.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')
  },

  /**
   * URL
   */
  getUrlKey(url, name) {
    return (
      decodeURIComponent(
        (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || ['', ''])[1].replace(/\+/g, '%20')
      ) || null
    )
  },
  /**
   * 
   */
  getCapMoney(num) {
    let n = Math.abs(num)
    if (n == '' || !/^(0|[1-9]\d*)(\.\d+)?$/.test(n)) {
      return ''
    }

    let unit = '',
      str = ''
    n += '00'
    let p = n.indexOf('.')
    if (p >= 0) n = n.substring(0, p) + n.substr(p + 1, 2)

    unit = unit.substr(unit.length - n.length)

    for (let i = 0; i < n.length; i++) str += ''.charAt(n.charAt(i)) + unit.charAt(i)
    str = str
      .replace(/(|||)/g, '')
      .replace(/()+/g, '')
      .replace(/(||)/g, '$1')
      .replace(/()|()/g, '$1$2')
      .replace(/^?|/g, '')
      .replace(/$/g, '')
    if (num < 0) str = '' + str
    return str
  },
  /**
   * 
   * @param {*} str 
   * @param {*} _type 
   */
  format_date(str, _type = 0) {
    if (!str) {
      return ''
    }
    if (str.toString().length < 12) {
      str = str * 1000
    }
    var date = new Date(str)
    var y = date.getFullYear()
    var m = date.getMonth() + 1
    m = m < 10 ? '0' + m : m
    var d = date.getDate()
    d = d < 10 ? '0' + d : d
    var h = date.getHours()
    h = h < 10 ? '0' + h : h
    var minute = date.getMinutes()
    var second = date.getSeconds()
    minute = minute < 10 ? '0' + minute : minute
    second = second < 10 ? '0' + second : second
    if (_type == '0') {
      return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second
    } else if (_type == '1') {
      return y + '-' + m + '-' + d + ' ' + h + ':' + minute
    } else if (_type == '2') {
      return m + '-' + d + ' ' + h + ':' + minute
    } else if (_type == '3') {
      return h + ':' + minute
    } else if (_type == '-1') {
      return date
    }
  },
  /**
   * input
   * @param {string} 
   * */
  checkTel(num = null, e) {
    if (num == null) return e
    if (e.length > num) {
      return e.slice(0, num)
    } else {
      return e
    }
  }
}
