<template>
  <div class="container">
    <page-title first-title=""></page-title>

    <div class="main">
      <el-form class="list" ref="form" label-width="100px">
        <el-form-item label="" v-if="type != 'create'">
          <el-input v-model="multiStatementNo" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            :autosize="{ minRows: 1 }"
            class="statement-nos"
            v-model="statementNostr"
            :disabled="true"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-input v-model="allIncome" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="allExpend" :disabled="true"></el-input>
        </el-form-item> -->
        <el-form-item label="" v-if="statementStatus > 0">
          <el-input v-model="addtimeFmt" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="statementStatus == 4">
          <el-input v-model="lastModifiedFmt" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="allCashIncome" :disabled="true"></el-input>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-input v-model="allHuanbiIncome" :disabled="true"></el-input>
        </el-form-item> -->
        <el-form-item label="">
          <el-input v-model="allCashExpend" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="realCash" :disabled="true"></el-input>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-input v-model="allHuanbiExpend" :disabled="true"></el-input>
        </el-form-item> -->
        <el-form-item label="">
          <el-input v-model="suppliersAccount" :disabled="type == 'show' ? true : false"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="payName" placeholder="" :disabled="type == 'show' ? true : false">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="payee" :disabled="type == 'show' ? true : false" key="payee"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="number"
            v-model="realPay"
            :disabled="type == 'show' ? true : false"
            @blur="toDecimal('realPay')"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            class="input-number"
            type="number"
            v-model="serviceFee"
            :disabled="type == 'show' ? true : false"
            @blur="toDecimal('serviceFee')"
          ></el-input>
        </el-form-item>
        <el-form-item label="" v-if="type == 'show'">
          <el-input v-model="statusList[statementStatus].info" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="statementStatus == 4">
          <el-input
            type="textarea"
            :autosize="{ minRows: 3 }"
            class="statement-nos"
            v-model="settlementLog"
            :disabled="true"
          ></el-input>
        </el-form-item>
        <el-form-item label="" v-if="statementStatus == 3 && (role == 'finance' || role == 'all')">
          <el-input type="textarea" :autosize="{ minRows: 3 }" class="statement-nos" v-model="settlementLog"></el-input>
        </el-form-item>
        <el-form-item v-if="type == 'create' && suppliersId != 0">
          <el-button type="primary" v-if="type == 'create'" @click="createMultiStatement"></el-button>
        </el-form-item>
        <el-form-item v-if="role == 'operater' || role == 'finance' || role == 'all'">
          <el-button
            type="primary"
            @click="confirmMultiStatement(2)"
            v-if="type == 'show' && statementStatus == 1 && (role == 'operater' || role == 'all')"
            ></el-button
          >
          <el-button
            type="primary"
            @click="confirmMultiStatement(4)"
            v-if="type == 'show' && statementStatus == 3 && (role == 'finance' || role == 'all')"
            ></el-button
          >
          <!-- <el-button type="primary" v-if="type == 'show'" @click="deleteMultiStatement"></el-button> -->
        </el-form-item>
        <el-form-item v-if="role == 'supplier'">
          <el-button type="primary" v-if="type == 'show' && statementStatus == 2" @click="confirmMultiStatement(3)"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Decimal } from 'decimal.js'
import {
  getSuppliersInfo,
  getSuppliersMultiStatementInfo,
  createMultiStatement,
  confirmMultiStatement,
  getLastMultiStatementInfo
} from '../../api/suppliers'
import { PAY_METHODS } from '../../const/status'

export default {
  name: 'MultiSuppliersStatementForm',

  components: {},

  data() {
    return {
      type: 'create', //  create: show 
      statements: null,
      statementNoArr: [], // 

      multiStatementNo: '', // 
      statementIds: [], // ID
      statementNos: '', // 
      allIncome: null, // 
      allExpend: null, // 
      allCashIncome: null, // 
      allHuanbiIncome: null, // 
      allCashExpend: null, // 
      allHuanbiExpend: null, // 
      addtime: 0, // 
      lastModified: 0, // 
      role: '', // operaterfinancesupplieranonymous, all
      realCash: null, // =-
      settlementLog: '', // 

      suppliersAccount: '', // 
      payName: '', // 
      payee: '', // 
      realPay: '0.00', // 
      serviceFee: '0.00', // 
      statementStatus: '0', // 
      statusList: {
        '0': { info: '' },
        '1': { info: '', role: '' },
        '2': { info: '', role: '' },
        '3': { info: '', role: '' },
        '4': { info: '' }
      }
    }
  },

  computed: {
    ...mapState({ suppliersId: state => state.suppliers.suppliers_id }),
    statementNostr() {
      var nos = ''
      for (var i in this.statementNoArr) {
        if (nos == '') {
          nos = this.statementNoArr[i]
        } else {
          nos += ',' + this.statementNoArr[i]
        }
      }
      return nos
    },
    addtimeFmt() {
      return this.dateFormat(this.addtime)
    },
    lastModifiedFmt() {
      return this.dateFormat(this.lastModified)
    },
    showSuppliersId() {
      return this.$route.params.suppliersId ? this.$route.params.suppliersId : this.suppliersId
    }
  },

  created() {
    console.log(this.$route.params.type)
    this.type = this.$route.params.type

    if (this.type == 'create') {
      this.getLastMultiStatementInfo()

      this.statements = this.$route.params.statements

      for (let i in this.statements) {
        this.statementNoArr.push(this.statements[i].statement_no)
      }

      this.statementIds = Object.keys(this.statements)

      // 
      var allIncome = new Decimal(0.0)
      var allExpend = new Decimal(0.0)
      var allCashIncome = new Decimal(0.0)
      var allHuanbiIncome = new Decimal(0.0)
      var allCashExpend = new Decimal(0.0)
      var allHuanbiExpend = new Decimal(0.0)

      for (let i in this.statements) {
        allIncome = allIncome.plus(this.statements[i].allAdd)
        allExpend = allExpend.plus(this.statements[i].allSub)
        allCashIncome = allCashIncome.plus(this.statements[i].add_cash)
        allHuanbiIncome = allHuanbiIncome.plus(this.statements[i].add_huanbi)
        allCashExpend = allCashExpend.plus(this.statements[i].sub_cash)
        allHuanbiExpend = allHuanbiExpend.plus(this.statements[i].sub_huanbi)
      }

      this.allIncome = allIncome.toFixed(2)
      this.allExpend = allExpend.toFixed(2)
      this.allCashIncome = allCashIncome.toFixed(2)
      this.allHuanbiIncome = allHuanbiIncome.toFixed(2)
      this.allCashExpend = allCashExpend.toFixed(2)
      this.allHuanbiExpend = allHuanbiExpend.toFixed(2)
      this.realCash = allCashIncome.sub(allCashExpend).toFixed(2)

      // 
    } else {
      this.multiStatementNo = this.$route.params.multiStatementNo
      this.getMultiStatementInfo()
    }
  },

  methods: {
    // 
    getSuppliersInfo() {
      getSuppliersInfo(this.suppliersId).then(
        res => {
          this.suppliersInfo = res
        },
        err => {
          this.$alert(err.errorCode + ':' + err.errorMsg, '', {
            confirmButtonText: ''
          })
        }
      )
    },

    getMultiStatementInfo() {
      getSuppliersMultiStatementInfo({
        suppliersId: this.showSuppliersId,
        multiStatementNo: this.multiStatementNo
      }).then(
        res => {
          this.multiStatementNo = res.multi_statement_no
          this.statementNoArr = res.relatedStatements
          this.allIncome = res.all_income
          this.allExpend = res.all_expend
          this.allCashIncome = res.all_cash_income
          this.allHuanbiIncome = res.all_huanbi_income
          this.allCashExpend = res.all_cash_expend
          this.allHuanbiExpend = res.all_huanbi_expend
          this.suppliersAccount = res.suppliers_account
          this.payName = PAY_METHODS[res.pay_name]
          this.payee = res.payee
          this.realPay = res.real_pay
          this.serviceFee = res.service_fee
          this.statementStatus = res.status
          this.addtime = res.addtime
          this.lastModified = res.last_modified
          this.role = res.role
          this.realCash = res.real_cash
          this.settlementLog = res.settlement_log
          console.log(this.statusList[this.statementStatus])
        },
        err => {
          this.$alert(err.errorCode + ':' + err.errorMsg, '', {
            confirmButtonText: ''
          })
        }
      )
    },

    // 
    getLastMultiStatementInfo() {
      getLastMultiStatementInfo({
        suppliersId: this.showSuppliersId
      }).then(
        res => {
          this.suppliersAccount = res.suppliers_account
          this.payName = res.pay_name
          this.payee = res.payee
        },
        err => {
          console.log(err)
        }
      )
    },

    // decimal
    toDecimal(key) {
      this[key] = this[key] ? this[key] : 0
      this[key] = new Decimal(this[key]).toFixed(2)
    },

    // 
    createMultiStatement() {
      if (this.suppliersAccount == '' || !this.suppliersAccount) {
        this.$alert('', '', {
          confirmButtonText: ''
        })
        return
      }

      if (this.payName == '' || !this.payName) {
        this.$alert('', '', {
          confirmButtonText: ''
        })
        return
      }

      if (this.payee == '' || !this.payee) {
        this.$alert('', '', {
          confirmButtonText: ''
        })
        return
      }

      if (this.realPay == '') {
        this.$alert('', '', {
          confirmButtonText: ''
        })
        return
      }

      if (this.serviceFee == '') {
        this.$alert('', '', {
          confirmButtonText: ''
        })
        return
      }

      if (this.realCash != '0' && this.realCash != '0.00' && (this.realPay == '0' || this.realPay == '0.00')) {
        this.$alert('0', '', {
          confirmButtonText: ''
        })
        return
      }

      if (
        new Decimal(this.allCashIncome)
          .sub(this.allCashExpend)
          .sub(this.realPay)
          .sub(this.serviceFee)
          .toFixed(2) *
          1 <
        0
      ) {
        this.$alert('" + "" - "', '', {
          confirmButtonText: ''
        })
        return
      }

      var multiStatementNo = ''

      createMultiStatement({
        suppliersId: this.suppliersId,
        statementIds: this.statementIds,
        suppliersAccount: this.suppliersAccount,
        payName: this.payName,
        payee: this.payee,
        realPay: this.realPay,
        serviceFee: this.serviceFee
      }).then(
        res => {
          multiStatementNo = res.multiStatementNo
          this.$alert('' + res.multiStatementNo, '', {
            confirmButtonText: ''
          }).then(() => {
            this.$route.params.type = 'show'
            this.$route.params.multiStatementNo = multiStatementNo
            // this.$router.replace({
            //   name: 'MultiSuppliersStatementList'
            // })
            this.$root.reload()
          })
        },
        err => {
          this.$alert(err.errorCode + ':' + err.errorMsg, '', {
            confirmButtonText: ''
          })
        }
      )
    },

    // 
    confirmMultiStatement(status) {
      confirmMultiStatement({
        suppliersId: this.showSuppliersId,
        multiStatementNo: this.multiStatementNo,
        status: status,
        settlementLog: this.settlementLog
      }).then(res => {
        this.$alert(res.info, '', {
          confirmButtonText: ''
        }).then(() => {
          this.$root.reload()
        })
      })
    },

    // table
    dateFormat: function(timestamp) {
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', timestamp * 1e3)
    },

    // 
    deleteMultiStatement() {}
  }
}
</script>

<style lang="scss" scoped>
// input typenumber
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
}
input[type='number'] {
  -moz-appearance: textfield;
}

.container {
  font-size: 15px;

  .main {
    background-color: #fff;

    .list {
      width: 25%;
      padding: 10px;
      margin: 10px 0 10px 10%;
    }
  }
}
</style>
