const state = {
  info_flag: false, // 
  activity_state: null, // 
  agreeDialogVisible: false, // 
  stopDialogVisible: false, // 
  params_info: {}, // 
  appr_infos: {}, // 
  stop_reason: '',
  stop_date: '',

  // props
  committime: '',
  successcount: 0,

  // 
  actInfos: {
    actId: '',
    actName: '',
    actDesc: '',
    startTime: '',
    endTime: ''
  },
  // 
  goods_lists: []
}

const mutations = {
  setActivityInfos(state, activityInfos) {
    state.actInfos = activityInfos
    state.params_info = JSON.parse(JSON.stringify(state.actInfos))
    // 
    state.params_info.startTime = new Date(state.params_info.startTime).getTime() / 1000
    state.params_info.endTime != ''
      ? (state.params_info.endTime = new Date(state.params_info.endTime).getTime() / 1000)
      : ''
  },
  actInfoFilled(state, flag) {
    state.info_flag = flag
  },
  actStateChange(state, status) {
    state.activity_state = status
  },
  showAgreeDialog(state) {
    // Boolean(Number(state.agreeDialogVisible) - 1)
    state.agreeDialogVisible = !state.agreeDialogVisible
  },
  showStopDialog(state) {
    state.stopDialogVisible = !state.stopDialogVisible
  },
  setStopReason(state, value) {
    state.stop_reason = value
  },
  setStopDate(state, date) {
    state.stop_date = date
  },
  setApprInfos(state, appr) {
    state.appr_infos = appr
  },
  updateGoodsList(state, goods) {
    state.goods_lists = goods
  },
  setCommitTime(state, time) {
    state.committime = time
  },
  setSpuCount(state, count) {
    state.successcount = count
  }
}

export default {
  state,
  mutations
}
